.comment {
	&__top {
		margin-bottom: 20px;
		padding-bottom: 20px;
		border-bottom: 2px solid var(--wildsand-color);
	}

	&__author {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__left {
		display: flex;
	}

	&__image {
		margin-right: 10px;
		border-radius: 100%;
		width: 70px;
		height: 70px;
		overflow: hidden;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		line-height: 150%;
		letter-spacing: 0.36px;
	}

	&__name {
		margin: 0;
		font-size: 22px;
		color: var(--cloudburst-color);
	}

	&__address {
		margin: 0;
		font-size: 18px;
		color: var(--gray-color);
	}

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		width: 55px;
		height: 55px;
		background-color: var(--orange-color);

		svg {
			width: 23px;
			height: 17px;
		}
	}

	&__content {}

	&__text {
		margin: 0;
		margin-bottom: 20px;
		font-weight: 700;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--gray-color);
	}

	&__btns {
		display: flex;
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		width: 50px;
		height: 50px;
		background-color: var(--wildsand-color);
		transition: background-color 0.3s ease-in-out, fill 0.3s ease-in-out;

		&:not(:last-child) {
			margin-right: 20px;
		}

		&--prev {
			svg {
				width: 14px;
				height: 20px;
				object-fit: cover;
			}
		}

		&--next {
			svg {
				width: 14px;
				height: 20px;
				object-fit: cover;
			}
		}

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				fill: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}
	}
}