.clients {
	padding-bottom: 120px;

	&__container {}

	&__centered {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__caption {
		margin-bottom: 30px;
	}

	&__title {
		margin-bottom: 50px;
	}

	@include tablet {
		padding-bottom: 80px;
	}
}

.clients-list {
	display: flex;
	justify-content: space-between;

	&__item {
		@include tablet {
			grid-column: 3 span;
		}

		@include mobile {
			&:not(:last-child) {
				margin-bottom: 30px;
			}
		}
	}

	&__inner {
		max-width: 211px;
		height: 55px;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	@include tablet {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		gap: 30px;
	}

	@include mobile {
		display: block;
	}
}