.progress {
	padding-bottom: 120px;
	background-color: var(--wildsand-color);

	&__container {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
	}

	&__col {
		grid-column: 6 span;

		@include tablet {
			grid-column: 12 span;
		}

		&--right {
			padding: 60px;
			background-color: var(--light-color);

			@include mobile {
				margin: 0 -15px;
				padding: 40px 15px;
			}
		}
	}

	&__box {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 800px;
		background-image: url('/img/progress-bg.jpg');
		background-position: top;
		background-size: cover;
		background-repeat: no-repeat;

		@include mobile {
			margin: 0 -15px;
			height: auto;
		}
	}

	&__list {}

	&__caption {
		margin-bottom: 20px;
		background-color: var(--wildsand-color);
	}

	&__title {
		margin-bottom: 20px;
	}

	&__descr {
		margin: 0;
		margin-bottom: 40px;
		font-size: 17px;
		line-height: 158%;
		letter-spacing: 0.36px;
		color: var(--gray-color);
	}

	&__information {}

	@include tablet {
		padding-bottom: 80px;
	}
}

.list-progress {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 0 auto;
	padding: 50px 30px;
	border-radius: 4px;
	width: 450px;
	height: 500px;
	background: rgba(28, 39, 82, 0.86);

	&__item {
		display: flex;
		align-items: center;
		width: 100%;
		height: 70px;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	&__left {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px 0;
		border-radius: 4px 0 0 4px;
		width: 110px;
		height: 100%;
		background-color: var(--orange-color);
	}

	&__value {
		font-size: 24px;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);
	}

	&__right {
		display: flex;
		align-items: center;
		padding: 0 20px;
		border-radius: 0 4px 4px 0;
		border: 1px solid var(--light-color);
		border-left: none;
		width: 100%;
		height: 100%;
	}

	&__descr {
		margin: 0;
		font-size: 25px;
		line-height: 140%;
		letter-spacing: 0.36px;
		color: var(--light-color);

		@include mobile {
			font-size: 22px;
		}
	}

	@include mobile {
		padding: 20px;
		width: 100%;
		height: auto;
	}
}

.information-list {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;

	&__item {
		display: flex;
		align-items: flex-start;

		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-shrink: 0;
		border-radius: 100%;
		width: 32px;
		height: 32px;
		background-color: var(--orange-color);

		svg {
			width: 20px;
			height: 17px;
		}

		@include mobile {
			display: none;
		}
	}

	&__content {
		margin-left: 20px;

		@include mobile {
			margin-left: 0;
		}
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 10px;
		font-weight: 400;
		font-size: 20px;
		line-height: 140%;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);
	}

	&__text {
		margin: 0;
		font-size: 17px;
		line-height: 159%;
		letter-spacing: 0.36px;
		color: var(--gray-color);
	}
}