.testimonials {
	background-color: var(--light-color);

	&__container {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		gap: 40px;

		@include tablet {
			grid-template-columns: repeat(6, 1fr);
		}
	}

	&__left {
		grid-column: 6 span;
	}

	&__caption {
		margin-bottom: 20px;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__inner {}

	&__top {
		margin-bottom: 40px;
		padding: 40px;
		border-radius: 4px;
		border: 2px solid var(--wildsand-color);

		@include mobile {
			padding: 0;
			border: none;
		}
	}

	&__list {}

	&__item {}

	&__bottom {
		padding: 50px 40px;
		border-radius: 4px;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);
		background-color: var(--orange-color);

		&>:not(:last-child) {
			margin-bottom: 20px;
		}

		@include mobile {
			margin: 0 -15px;
			padding: 40px 15px;
		}
	}

	&__subtitle {
		margin: 0;
		font-size: 25px;
		line-height: 130%;
	}

	&__descr {
		margin: 0;
		font-size: 18px;
		line-height: 150%;
	}

	&__link {
		display: flex;
		align-items: center;
		font-size: 30px;
		letter-spacing: 0.36px;
		color: var(--light-color);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--cloudburst-color);
			}
		}

		@include mobile {
			font-size: 18px;
		}
	}

	&__icon {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		margin-right: 20px;
		border-radius: 100%;
		width: 50px;
		height: 50px;
		background-color: var(--light-color);

		img {
			width: 26px;
			height: 26px;
			object-fit: cover;
		}
	}

	&__right {
		grid-column: 6 span;
		padding: 60px 40px;
		border-radius: 4px;
		background-color: var(--wildsand-color);

		@include mobile {
			margin: 0 -15px;
			padding: 40px 15px;
		}
	}

	&__form {}
}

.form-field {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__input {
		padding: 20px;
		border-radius: 4px;
		width: 100%;
		height: 65px;
		resize: none;
		color: var(--mineshaft-color);

		&:not(:last-child) {
			margin-bottom: 30px;
		}

		&::placeholder {
			font-size: 17px;
			letter-spacing: 0.36px;
			color: var(--gray-color);
		}
	}

	&__input--big {
		height: 150px;
	}

	&__btn {
		margin-top: auto;
		width: 100%;
		font-weight: 700;
		font-size: 17px;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}
	}
}