.subscribe {
	background-color: var(--orange-color);

	&__container {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;

		@include tablet {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__content {
		padding-right: 20px;
		max-width: 410px;

		@include tablet {
			max-width: 100%;
			margin-bottom: 40px;
		}
	}

	&__title {
		margin-bottom: 20px;
		font-size: 32px;
	}

	&__descr {
		margin: 0;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);

	}

	&__form {
		display: flex;
		align-items: center;
		border: 1px solid var(--light-color);
		border-radius: 4px;
		width: 600px;
		overflow: hidden;
	}

	&__input {
		display: flex;
		align-items: center;
		padding: 20px;
		width: 100%;
		background-color: var(--orange-color);

		&::placeholder {
			font-size: 17px;
			letter-spacing: 0.36px;
			color: var(--light-color);
		}

	}

	&__btn {
		display: flex;
		align-items: center;
		padding: 20px 40px;
		border-radius: 0 4px 4px 0;
		font-size: 17px;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);
		background-color: var(--light-color);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		@include for-desktop {
			&:hover {
				color: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}

		@include mobile {
			padding: 20px;
		}
	}
}