html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

.page {
	height: 100%;
	font-family: var(--font-family, sans-serif);
	-webkit-text-size-adjust: 100%;
}

.page__body {
	margin: 0;
	min-width: 320px;
	min-height: 100%;
	font-size: 16px;
}

img {
	height: auto;
	max-width: 100%;
	object-fit: cover;
}

a {
	text-decoration: none;
}

.site-container {
	overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
	display: none !important;
	/* stylelint-disable-line declaration-no-important */
}

.btn-reset {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

.list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

.input-reset {
	-webkit-appearance: none;
	appearance: none;
	border: none;
	border-radius: 0;
	background-color: #fff;

	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	border: 0;
	padding: 0;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.container {
	margin: 0 auto;
	padding: 0 var(--container-offset);
	max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}

.centered {
	text-align: center;
}

.dis-scroll {
	position: fixed;
	left: 0;
	top: 0;
	overflow: hidden;
	width: 100%;
	height: 100vh;
	overscroll-behavior: none;
}

.page--ios .dis-scroll {
	position: relative;
}

.section-offset {
	padding: 120px 0;

	@include tablet {
		padding: 80px 0;
	}
}

.btn {
	display: inline-block;
	padding: 20px 40px;
	border-radius: 4px;
	font-weight: 400;
	font-size: 20px;
	line-height: 150%;
	letter-spacing: 0.36px;
	color: var(--cloudburst-color);
	background-color: var(--orange-color);
}

.title {
	margin: 0;
	font-weight: 400;
	font-size: 45px;
	line-height: 130%;
	letter-spacing: 0.36px;
	color: var(--cloudburst-color);

	@include mobile {
		font-size: 36px;
	}
}

.caption {
	display: inline-flex;
	align-items: center;
	padding: 5px 20px;
	border-radius: 4px;
	font-size: 18px;
	letter-spacing: 0.36px;
	text-transform: capitalize;
	color: var(--cloudburst-color);
	background-color: var(--wildsand-color);

	&__image {
		display: block;
		margin-right: 10px;
	}
}