.our-services {
	padding-top: 180px;
	padding-bottom: 120px;
	background-color: var(--wildsand-color);

	&__container {}

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 50px;
	}

	&__caption {
		margin-bottom: 20px;
		background-color: var(--light-color);
	}

	&__title {
		margin-bottom: 20px;
	}

	&__descr {
		margin: 0;
		max-width: 820px;
		font-size: 18px;
		line-height: 150%;
		text-align: center;
		letter-spacing: 0.36px;
		color: var(--gray-color);
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		gap: 30px;

		@include mobile {
			display: block;

			&>:not(:last-child) {
				margin-bottom: 30px;
			}
		}
	}

	&__item {
		grid-column: 4 span;
		grid-auto-rows: 380px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		padding: 45px 35px;
		border-radius: 4px;
		background-color: var(--cloudburst-color);

		&>:not(:last-child) {
			margin-bottom: 20px;
		}

		@include tablet {
			grid-column: 6 span;
		}

		@include mobile {
			margin: -15px;
			padding: 40px 15px;
		}
	}

	&__image {
		display: block;
		width: 48px;
		height: 48px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__subtitle {
		margin: 0;
		font-weight: 400;
		font-size: 25px;
		line-height: 150%;
		letter-spacing: 0.36px;
		text-transform: capitalize;
		color: var(--orange-color);
	}

	&__text {
		margin: 0;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--wildsand-color);
	}

	&__more {
		margin-top: auto;
		font-weight: 400;
		font-size: 22px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--light-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--orange-color);
			}
		}
	}

	@include tablet {
		padding: 80px 0;

	}
}