.benefits {

	&__container {
		display: flex;
		justify-content: space-between;

		@include desktop {
			flex-wrap: wrap;
		}
	}

	&__image {
		flex-shrink: 0;
		margin-right: 50px;
		width: 575px;


		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@include desktop {
			margin-right: 0;
			margin-bottom: 20px;
			width: 100%;
			height: 580px;
		}

		@include mobile {
			height: 300px;
		}
	}

	&__content {}

	&__caption {
		margin-top: 20px;
		margin-bottom: 20px;
	}

	&__title {
		margin-bottom: 10px;
	}

	&__text {
		margin-bottom: 20px;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--gray-color);

		p {
			margin: 0;

			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}
	}

	&__bottom {
		display: flex;
		align-items: center;

		@include mobile {
			display: block;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin-right: 40px;
		padding: 0 40px;
		width: 200px;
		height: 220px;
		background-color: var(--wildsand-color);

		@include mobile {
			width: 100%;
			margin-bottom: 20px;
		}
	}

	&__value {
		display: inline-block;
		margin-bottom: 10px;
		font-size: 80px;
		letter-spacing: 0.36px;
		color: var(--orange-color);
	}

	&__subtitle {
		font-size: 20px;
		line-height: 130%;
		letter-spacing: 0.36px;
		text-align: center;
		color: var(--cloudburst-color);
	}

	&__right {}

	&__list {}

	&__item {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__descr {
		position: relative;
		margin: 0;
		padding-left: 15px;
		font-size: 17px;
		line-height: 159%;
		letter-spacing: 0.36px;
		color: var(--gray-color);

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 7px;
			height: 1px;
			background-color: var(--gray-color);
			transform: translateY(-50%);

			@include mobile {
				display: none;
			}
		}

		@include mobile {
			padding-left: 0;
		}
	}
}