.footer {
	padding-top: 60px;
	padding-bottom: 60px;
	background-color: var(--cloudburst-color);
	color: var(--light-color);

	&__container {}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-bottom: 30px;
		border-bottom: 1px solid var(--light-color);

		@include desktop {
			flex-wrap: wrap;
		}
	}

	&__logo {
		margin-right: 20px;

		@include desktop {
			margin-right: 0;
			margin-bottom: 40px;
		}
	}

	&__contacts {
		flex-wrap: wrap;
		height: 100%;
	}

	&__bottom {
		display: flex;
		padding-top: 40px;

		@include tablet {
			display: block;
		}
	}

	&__left {
		margin-right: 40px;
		max-width: 400px;

		@include tablet {
			margin-right: 0;
			margin-bottom: 40px;
			min-width: 100%;
		}
	}

	&__descr {
		margin: 0;
		margin-bottom: 50px;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
	}

	&__social {}

	&__nav {}

	.contacts__caption {
		color: var(--light-color);
	}

	.contacts__value {
		color: var(--light-color);
		transition: color 0.3s ease-in-out;

		@include for-desktop {
			&:hover {
				color: var(--orange-color);
			}
		}
	}

	.contacts__link--map::before {
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		border-radius: 100%;
		width: 68px;
		height: 68px;
		background-color: var(--orange-color);
		background-image: url('/img/map.png');
		background-position: center;
		background-size: 22px 32px;
		background-repeat: no-repeat;
		transform: translateY(-50%);
	}

	.contacts__link {

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		@include tablet {
			margin-bottom: 30px;
		}
	}

	.social__item {
		border-radius: 100%;
		width: 60px;
		height: 60px;
		background-color: #2C3765;
	}

	.social__link {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		width: 100%;
		height: 100%;
		transition: background-color 0.3s ease-in-out;

		svg {
			width: 20px;
			height: 20px;
			fill: var(--light-color);
		}

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
			border-radius: 100%;
		}

		@include for-desktop {
			&:hover {
				background-color: var(--orange-color);
			}
		}
	}
}

.nav-footer {
	display: flex;
	width: 100%;

	&__col {
		flex: 1 1 50%;

		&:not(:last-child) {
			padding-right: 30px;

			@include mobile {
				margin-bottom: 40px;
			}
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 20px;
		font-weight: 400;
		font-size: 20px;
		letter-spacing: 0.36px;
		color: var(--orange-color);
	}

	&__list {}

	&__item {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__link {
		font-size: 17px;
		letter-spacing: 0.36px;
		color: var(--light-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--orange-color);
			}
		}
	}

	@include mobile {
		flex-wrap: wrap;
	}
}