.news {
	background-color: var(--light-color);

	&__container {}

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__caption {
		margin-bottom: 20px;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__box {
		display: flex;
		margin: 0 -15px;

		@include tablet {
			// display: block;
			flex-wrap: wrap;
		}
	}
}

.news-preview {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	padding: 0 15px;

	&__image {
		max-width: 580px;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__meta {
		position: relative;
		left: 50%;
		display: flex;
		flex-wrap: wrap;
		margin-top: -30px;
		margin-bottom: 40px;
		z-index: 10;
		transform: translateX(-50%);
	}

	&__name {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px 0;
		width: 180px;
		background-color: var(--orange-color);

		svg {
			display: block;
			margin-right: 10px;
			width: 20px;
			height: 20px;
			fill: var(--cloudburst-color);
		}

		@include mobile {
			padding: 10px;
		}
	}

	&__date {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px 0;
		width: 180px;
		background-color: #FFC368;

		svg {
			display: block;
			margin-right: 10px;
			width: 20px;
			height: 20px;
			fill: var(--cloudburst-color);
		}

		@include mobile {
			padding: 10px;
		}
	}

	&__caption {
		font-size: 15px;
		letter-spacing: 0.3px;
		color: var(--cloudburst-color);
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 20px;
		font-size: 30px;
	}

	&__text {
		margin: 0;
		margin-bottom: 20px;
		font-size: 17px;
		line-height: 159%;
		letter-spacing: 0.36px;
		color: var(--gray-color);
	}

	&__more {
		display: flex;
		align-items: center;
		font-size: 20px;
		letter-spacing: 0.4px;
		color: var(--cloudburst-color);
		outline: none;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		span {
			display: inline-block;
			margin-right: 10px;
		}

		svg {
			display: block;
			width: 10px;
			height: 15px;
			fill: var(--cloudburst-color);
		}

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--orange-color);
			}
		}
	}

	@include tablet {
		display: block;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 40px;
		}
	}

	@include mobile {
		display: block;
	}
}