.nav {
	position: relative;
	z-index: 10;
	margin-top: -30px;

	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10px 10px 10px 40px;
		border-radius: 4px;
		background-color: var(--orange-color);
	}

	&__list {
		display: flex;
	}

	&__item {
		&:not(:last-child) {
			margin-right: 50px;
		}
	}

	&__link {
		position: relative;
		font-size: 19px;
		line-height: 158%;
		letter-spacing: 0.36px;
		color: var(--mineshaft-color);
		outline: none;

		&--active {
			color: var(--light-color);
		}

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background-color: currentColor;
			transform-origin: left;
			transform: scaleX(0);
			transition: transform 0.3s ease-in-out;
		}

		&:focus {
			&::before {
				transform: scaleX(1);
			}
		}

		@include for-desktop {
			&:hover {
				&::before {
					transform: scaleX(1);
				}
			}
		}
	}

	&__questions {
		padding: 20px 40px;
		border-radius: 4px;
		font-size: 20px;
		letter-spacing: 0.36px;
		color: var(--light-color);
		background-color: var(--cloudburst-color);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--cloudburst-color);
				background-color: var(--light-color);
			}
		}
	}

	@include tablet {
		display: none;
	}
}