.hero {
	margin-top: -60px;
	padding-top: 260px;
	padding-bottom: 220px;
	background-image: url('/img/hero-bg.jpg');
	background-position: left top;
	background-size: cover;
	background-repeat: no-repeat;

	&__container {}

	&__content {}

	&__image {
		margin-right: 10px;
	}

	&__descr {
		display: inline-flex;
		align-items: center;
		margin-bottom: 30px;
		padding-left: 20px;
		padding-right: 40px;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		text-transform: capitalize;
		color: var(--light-color);
		background: rgba(255, 255, 255, 0.2);

		@include mobile {
			font-size: 16px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 30px;
		max-width: 710px;
		font-weight: 400;
		font-size: 60px;
		line-height: 130%;
		letter-spacing: 1.2px;
		text-transform: capitalize;
		color: var(--light-color);

		@include mobile {
			font-size: 40px;
		}
	}

	&__link {
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}
	}

	@include tablet {
		margin: 0;
		padding-top: 80px;
		padding-bottom: 120px;
	}
}