.price {
	background-color: var(--wildsand-color);

	&__container {}

	&__top {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		margin-bottom: 60px;

		@include desktop {
			flex-wrap: wrap;
		}
	}

	&__left {
		padding-right: 20px;
		max-width: 500px;

		@include desktop {
			margin-bottom: 30px;
			max-width: 100%;
		}
	}

	&__caption {
		margin-bottom: 20px;
		background-color: var(--light-color);
	}

	&__title {}

	&__text {
		max-width: 585px;

		p {
			margin: 0;
			font-size: 18px;
			line-height: 150%;
			letter-spacing: 0.36px;
			color: var(--gray-color);
		}

		@include desktop {
			max-width: 100%;
		}
	}

	&__columns {
		display: flex;
		margin: 0 -15px;
		margin-bottom: 60px;

		@include desktop {
			flex-wrap: wrap;
			justify-content: center;
		}
	}


	&__column {
		padding: 0 15px;
		width: 100%;

		@include desktop {
			flex: 0 1 80%;

			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}

		@include mobile {
			flex: 1 1 100%;
		}
	}

	&__box {
		display: flex;
		flex-direction: column;
		padding: 40px;
		height: 100%;
		background-color: var(--light-color);

		@include mobile {
			padding: 20px;
		}
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 10px;
		font-size: 20px;
		line-height: 150%;
		letter-spacing: 0.4px;
		color: var(--cloudburst-color);
	}

	&__inner {
		position: relative;
		display: flex;
		align-items: flex-end;
		padding-bottom: 20px;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background-color: var(--gray-color);
		}

		@include mobile {
			align-items: center;
		}
	}

	&__value {
		margin-right: 30px;
		font-size: 32px;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);

		@include mobile {
			font-size: 22px;
		}
	}

	&__descr {
		font-size: 15px;
		line-height: 160%;
		letter-spacing: 0.32px;
		color: var(--gray-color);
	}

	&__btn {
		margin-top: auto;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}
	}

	&__bottom {
		display: flex;
		margin: 0 -15px;

		@include tablet {
			flex-wrap: wrap;
		}
	}

	.information-list {
		flex-direction: row;
		padding: 0 15px;

		&:not(:last-child) {
			@include tablet {
				margin-bottom: 40px;
			}
		}

		@include tablet {
			width: 80%;
		}

		@include mobile {
			width: 100%;
		}
	}
}

.column-price {
	margin-top: 30px;
	margin-bottom: 50px;

	&__item {
		padding-left: 31px;
		background-image: url('/img/check-price.png');
		background-position: left top;
		background-size: 19px 19px;
		background-repeat: no-repeat;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	&__service {
		margin: 0;
		font-size: 17px;
		letter-spacing: 0.36px;
		text-transform: capitalize;
		color: var(--gray-color);
	}
}

.information-list {
	display: flex;
	// flex-direction: row;
	// flex: 1 1 50%;

	&__icon {}

	&__content {}

	&__subtitle {}

	&__text {}
}