.projects {
	background-color: var(--cloudburst-color);

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;

		@include tablet {
			display: block;
		}
	}

	&__caption {
		margin-bottom: 20px;
	}

	&__title {
		margin-bottom: 50px;
		color: var(--light-color);
	}

	&__cards {}
}

.card {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	gap: 30px;

	&__item {
		grid-column: 3 span;
		padding: 10px 10px 25px 10px;
		background-color: var(--light-color);

		@include tablet {
			grid-column: 6 span;
			width: 100%;
		}
	}

	&__image {
		margin-bottom: 30px;

		img {
			display: block;
			width: 100%;
		}
	}

	&__inner {
		text-align: center;
		letter-spacing: 0.36px;

	}

	&__title {
		margin: 0;
		margin-bottom: 5px;
		font-weight: 400;
		font-size: 25px;
		line-height: 150%;
		text-transform: capitalize;
		color: var(--cloudburst-color);
	}

	&__descr {
		margin: 0;
		font-size: 17px;
		color: var(--gray-color);
	}

	@include mobile {
		display: block;

		&>:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	@include mobile {
		margin: 0 -15px;
	}
}