.header {
	position: relative;
	background-color: var(--wildsand-color);

	&__container {}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 20px 0;
	}

	&__time {
		font-size: 17px;
		line-height: 158%;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);

		@include mobile {
			margin-right: 10px;
		}
	}

	&__social {}

	&__center {
		padding-top: 30px;
		padding-bottom: 60px;
		background-color: var(--light-color);
	}

	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include tablet {
			flex-wrap: wrap;
		}
	}

	&__logo {
		width: 242px;
		height: 48px;

		@include tablet {
			margin-bottom: 50px;
		}
	}

	&__contacts {}
}

.social {
	&__list {
		display: flex;
	}

	&__item {
		width: 20px;
		height: 20px;

		&:not(:last-child) {
			margin-right: 20px;
		}
	}

	&__link {
		svg {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			fill: var(--cloudburst-color);
			transition: fill 0.3s ease-in-out;

			@include for-desktop {
				&:hover {
					fill: var(--orange-color);
				}
			}
		}
	}
}

.contacts {
	display: flex;
	height: 68px;

	&__link {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 83px;

		&:not(:last-child) {
			margin-right: 60px;

			@include desktop {
				margin-right: 30px;
			}

			@include mobile {
				flex: 1 0 100%;
				margin-bottom: 40px;
			}
		}
	}

	&__link--mail::before {
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		border-radius: 100%;
		width: 68px;
		height: 68px;
		background-color: var(--orange-color);
		background-image: url('/img/mail.png');
		background-position: center;
		background-size: 28px 22px;
		background-repeat: no-repeat;
		transform: translateY(-50%);
	}

	&__link--call::before {
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		border-radius: 100%;
		width: 68px;
		height: 68px;
		background-color: var(--orange-color);
		background-image: url('/img/phone.png');
		background-position: center;
		background-size: 28px 22px;
		background-repeat: no-repeat;
		transform: translateY(-50%);
	}

	&__caption {
		font-size: 20px;
		line-height: 130%;
		letter-spacing: 0.36px;
		color: var(--cloudburst-color);
	}

	&__value {
		font-size: 17px;
		line-height: 159%;
		letter-spacing: 0.36px;
		color: var(--gray-color);
		transition: color 0.3s ease-in-out;

		@include for-desktop {
			&:hover {
				color: var(--orange-color);
			}
		}
	}

	@include mobile {
		flex-wrap: wrap;
		height: 100%;
	}
}