.main-services {
	padding-top: 120px;
	height: 885px;
	background-color: var(--orange-color);

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;

		@include mobile {}
	}

	&__caption {
		margin-bottom: 30px;
	}

	&__title {
		margin-bottom: 15px;
	}

	&__descr {
		margin: 0;
		margin-bottom: 40px;
		max-width: 830px;
		text-align: center;

		@include mobile {
			text-align: start;
		}
	}

	&__btns {
		display: flex;
		margin-bottom: 40px;

		@include tablet {
			display: block;
			width: 100%;
		}
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px;
		border: 1px solid #1C2752;
		width: 235px;
		color: var(--mineshaft-color);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:not(:last-child) {
			margin-right: 30px;

			@include tablet {
				margin-right: 0;
				margin-bottom: 30px;
			}
		}

		&--active {
			border: none;
			background-color: var(--light-color);
		}

		img {
			display: block;
			margin-right: 10px;
		}

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--cloudburst-color);
				background-color: var(--light-color);
			}
		}

		@include tablet {
			width: 100%;
		}
	}

	&__commerce {
		position: relative;
	}

	@include tablet {
		padding: 80px 0;
		height: 100%;
	}
}

.commerce {
	display: flex;
	padding: 70px 60px;
	max-width: 1160px;
	box-shadow: -1px 6px 18px rgba(0, 0, 0, 0.09);
	background-color: var(--light-color);

	&__content {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		@include tablet {
			margin-bottom: 40px;
		}
	}

	&__title {
		margin-bottom: 20px;
	}

	&__text {
		margin-bottom: 30px;
		padding-right: 20px;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.36px;
		color: var(--gray-color);

		p {
			margin: 0;

			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}

		@include mobile {
			font-size: 16px;
		}
	}

	&__more {
		margin-top: auto;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--cloudburst-color);
			outline-offset: 2px;
		}

		@include for-desktop {
			&:hover {
				color: var(--light-color);
				background-color: var(--cloudburst-color);
			}
		}
	}

	&__image {
		flex-shrink: 0;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@include tablet {
			width: 100%;
			height: 400px;
		}

		@include mobile {
			height: 250px;
		}
	}

	@include tablet {
		flex-wrap: wrap;
		max-width: 100%;
		padding: 50px 40px;
	}

	@include mobile {
		padding: 40px 20px;
	}
}